.signup-page {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-block-start: rem(10px);

    height: 80vh;

    form {
        h1:first-child {
            align-self: center;
        }

        background-color: rgba($color: white, $alpha: 0.7);
        border-radius: rem(10px);
        padding: 10px;
        display: flex;
        flex-direction: column;
        min-width: 400px;
        margin-block-end: 20px;
        gap: 5px;

        input {
            border-radius: 10px;
            padding: 10px;
        }
        button {
            border-radius: 10px;
            padding: 10px;
            background-color: #7dd100;
            margin-block-start: 10px;
        }
    }

    @include for-mobile-layout {
        form {
            gap: 0;
            min-width: 0px;
            label {
                font-size: rem(16px);
            }
            input {
                font-size: rem(12px);

                width: rem(250px);
                height: rem(40px);
            }
        }
    }
}
