.contact-preview {
    display: flex;
    flex-direction: column;
    padding: 10px;
    border: 1px solid black;
    border-radius: 10px;
    width: 250px;
    height: 250px;
    background-color: rgba($color: rgb(255, 255, 254), $alpha: 0.7);

    // text-align: initial;
    justify-content: space-around;
    img {
        width: 50%;
        align-self: center;
    }
}
