.app-header {
    height: rem(80px);
    margin-inline: rem(70px);
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: rgba($color: white, $alpha: 0.5);
    border-radius: 10px;

    .home-logo {
        cursor: pointer;
    }

    img {
        width: 40px;
    }

    @include for-mobile-layout {
        margin-inline-start: 0;
        margin-inline-end: 0;
        justify-content: flex-start;
        div:last-child {
            margin-inline-start: auto;
            margin-inline-end: rem(10px);
        }
        div:first-child {
            margin-inline-start: rem(10px);
            // margin-inline-end: 10px;
        }

        .home-logo {
            img {
                width: 50px;
            }
        }
        .MuiBox-root {
            .MuiAvatar-root {
                // height: 60px;
                // width:60px;
            }
        }

        // margin-inline-end: rem(15px);
    }
}
