.contact-page {
    //   background-color: rgba($color: #fda93f, $alpha: 0.5);

    display: flex;
    flex-direction: column;
    align-items: center;
    gap: rem(20px);
    button {
        padding: rem(10px);
    }
    .add-contact {
        display: flex;
        align-items: center;
        gap: rem(10px);
    }

    @include for-mobile-layout {
        .add-contact {
            button {
                width: rem(50px);
                height: rem(50px);
                padding: 0;
            }
        }
    }
}
