.home-page {
    display: flex;
    flex-direction: column;
    gap: rem(40px);
    align-items: center;

    background-color: rgba($color: white, $alpha: 0.7);
    border-radius: rem(10px);
    padding: 10px;

    .guest-welcome {
        display: flex;
        flex-direction: column;
        gap: rem(10px);
        align-items: center;
        width: fit-content;
        // background-color: rgba($color: white, $alpha: 0.7);
        // padding: rem(10px);
        // border-radius: rem(10px);
    }

    .user-balance {
        display: flex;
        flex-direction: column;
        gap: rem(10px);
        // background-color: rgba($color: white, $alpha: 0.7);
        // border-radius: rem(10px);
        // padding: 10px;
    }

    &.logged-user {
        text-align: center;
        .MuiTable-root {
            background-color: aliceblue;
        }
    }

    button {
        width: fit-content;
    }

    @include for-mobile-layout {
        .guest-welcome {
            h1 {
                font-size: rem(23px);
            }
        }
    }
}
