.header-layout {
  padding: 0 15px;
  height: 50px;
}

.main-layout {
  display: grid;
  grid-template-columns: rem(70px) 1fr rem(70px);
  grid-template-rows: rem(80px) 1fr rem(70px);

  > * {
    grid-column: 2;
  }

  > .full {
    grid-column: 1 / -1;
  }

  @include for-narrow-layout {
    // gap: rem(10px);
    grid-template-columns: rem(5px) 1fr rem(5px);
    grid-template-rows: rem(80px) 1fr rem(60px);
  }

  // @include for-normal-layout {
  //   grid-template-columns:
  //     minmax(2rem, 1fr)
  //     minmax(auto, $break-normal)
  //     minmax(2rem, 1fr);
  // }

  // @include for-wide-layout {
  //   grid-template-columns:
  //     minmax(2rem, 1fr)
  //     minmax(auto, $break-wide)
  //     minmax(2rem, 1fr);
  // }
}

.main-content {
  flex: 1;
}
