.app-footer {
    background: linear-gradient(to bottom, rgba(255, 255, 255, 0.5) 0%, rgba(240, 248, 255, 0.5) 50%, #f0f8ff 100%);
    display: flex;
    align-items: center;
    justify-content: center;
    bottom: 2px;
    width: 100%;
    position: fixed;
    .contacts-btn {
        border-radius: 50%;
        background-color: #ffdea3;
    }

    @include for-mobile-layout {
        .contacts-btn {
            height: 50px;
            width: 50px;
        }
    }
}
