.transactions-table,
.contact-details-page {
    .MuiTable-root {
        min-width: 80vw;
    }

    @include for-mobile-layout {
        .MuiTableCell-root {
            min-width: 0px;
        }

        .MuiTable-root {
            min-width: auto;
        }
        margin-inline-start: 0;
        margin-inline-end: 0;
        .MuiTableCell-root {
            padding: rem(3px);
            font-size: rem(7px);
        }
    }
}
