.contact-details-page {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: rem(20px);
    padding: rem(10px);

    img {
        padding: rem(10px);
        width: rem(250px);
    }
    .action-buttons {
        width: 100%;
        display: flex;
        justify-content: space-between;
    }
    .contact-details {
        background-color: rgba($color: white, $alpha: 0.7);
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

    // @include for-mobile-layout {
    //     img {
    //         width: rem(250px);
    //     }
    // }
}
