.contact-edit {
    form {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: fit-content;
        margin: 20px auto;
        gap: 10px;
        .contact-name,
        .contact-email,
        .contact-phone {
            width: 100%;
            align-items: center;
            display: flex;
            justify-content: space-between;
            input {
                padding: 5px;
                text-align: center;
            }
        }
        button {
            margin-block-start: 10px;
        }
    }
}
